import request from '@/utils/request'

//获取文章列表
export const getArticleList = ({ current, sorter }) => {
  return request({
    method: 'GET',
    url: '/interview/query',
    params: {
      current,
      sorter,
      pageSize: 10,
    },
  })
}

//获取收藏列表
export const getArticleCollect = page => {
  return request({
    method: 'GET',
    url: '/interview/opt/list',
    params: {
      optType: 2,
      pageSize: 10,
      page,
    },
  })
}

//获取点赞列表
export const getLikeArticle = page => {
  return request({
    method: 'GET',
    url: '/interview/opt/list',
    params: {
      optType: 1,
      pageSize: 10,
      page,
    },
  })
}

//获取单个面经详情
export const getArticleDetail = id => {
  return request({
    method: 'GET',
    url: '/interview/show',
    params: {
      id,
    },
  })
}

//点赞题目/取消点赞题目接口
export const updateLike = id => {
  return request({
    method: 'POST',
    url: '/interview/opt',
    data: {
      id,
      optType: 1,
    },
  })
}

//收藏题目/取消收藏题目接口
export const updateCollect = id => {
  return request({
    method: 'POST',
    url: '/interview/opt',
    data: {
      id,
      optType: 2,
    },
  })
}

